.hamburgerMenuChild {
  width: 102px;
  position: relative;
  border-top: 1px solid #fff;
  box-sizing: border-box;
  height: 1px;
}
.hamburgerMenu {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 13px;
  mix-blend-mode: normal;
  z-index: 1;
}
.bingen {
  mix-blend-mode: normal;
  position: relative;
  top: 8%;
  z-index: 1;
}
.homescreenOnIphone13 {
  background-color: #000;
  text-align: left;
  font-size: 75px;
  color: #fff;
  font-family: Jomhuria, monospace;
  width: 100%;
  height: 100vh;
}

.topBar {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 10%;
  background-color: #000;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
}

@keyframes slideUp {
  from {
    transform: translateY(80%);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.slideUpAnimation {
  animation: slideUp 2s ease forwards;
  position: absolute;
  height: 100%;
  left: calc(50% - 258px);
  width: 517px;
  overflow-y: scroll;
  scrollbar-width: none;
  overflow-x: hidden;
}
